<template>
  <div ref="modal" class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel"
    aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">新增提醒事項</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">提醒事項主題</label>
            <input
              type="text"
              class="form-control"
              id="exampleInputEmail1"
              aria-describedby="emailHelp"
              v-model="todo.title"
            >
          </div>
          <div class="mb-3">
            <label for="exampleInputEmail1" class="form-label">提醒事項備註</label>
            <textarea
              class="form-control"
              id="exampleInputEmail1"
              aria-describedby="emailHelp"
              v-model="todo.content"
            ></textarea>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">取消</button>
          <button type="button" class="btn btn-primary" @click="$emit('handleSettingTodo', todo)">新增</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Modal from 'bootstrap/js/dist/modal'
import { getTodo } from '@/apis/todo_api'
export default {
  props: ['todoId'],
  data () {
    return {
      modal: '',
      todo: {
        id: '',
        title: '',
        content: ''
      }
    }
  },
  watch: {
    todoId () {
      if (this.todoId) {
        this.getTodo(this.todoId)
      }
    }
  },
  mounted () {
    this.modal = new Modal(this.$refs.modal)
  },
  methods: {
    showModal () {
      this.modal.show()
    },
    hideModal () {
      this.todo.title = ''
      this.todo.content = ''
      this.modal.hide()
    },
    async getTodo () {
      try {
        const res = await getTodo(this.todoId)
        const { title, content, _id } = res.data.data
        this.todo.title = title
        this.todo.content = content
        this.todo.id = _id
      } catch (err) {
        console.log(err)
      }
    }
  }
}
</script>
