<template>
  <div class="container">
    <div class="my-3">
      <section class="h4 text-center">提醒事項</section>
      <section class="mt-3 text-end">
        <button class="btn btn-info" @click="handleUpdateTodo(null)">新增提醒事項</button>
      </section>
      <section class="mt-3">
        <div class="card mb-3" v-for="todo in todoLists" :key="todo.id">
          <div class="card-header d-flex align-items-center">
            <h5 class="m-0">
              <i class="las la-check-square me-1" v-if="todo.finished"></i>
              {{ todo.title }}
            </h5>
            <button
              class="btn btn-secondary ms-auto"
              v-if="!todo.finished"
              @click="handleFinishedTodo(todo.id)"
            >完成</button>
          </div>
          <div class="card-body">
            <small class="text-muted">建立時間：{{ todo.create_date }}，</small>
            <small class="text-muted">最後更新時間：{{ todo.create_date }}</small>
            <br />
            <div class="mt-2">{{ todo.content }}</div>
          </div>
          <div class="card-footer p-3 text-end">
            <button class="btn btn-outline-secondary me-2" v-if="!todo.finished" @click="handleUpdateTodo(todo.id)">編輯</button>
            <button class="btn btn-outline-danger" @click="handleDeleteTodo(todo.id)">刪除</button>
          </div>
        </div>
      </section>
      <AddTodoModal ref="addTodoModal" :todoId="todoId" @handleSettingTodo="handleTempTodo"></AddTodoModal>
    </div>
  </div>
</template>

<script>
import AddTodoModal from '@/components/AddTodoModal.vue'
import {
  getTodos,
  finishedTodo,
  deleteTodo,
  createTodo,
  updateTodo
} from '@/apis/todo_api'
import moment from 'moment'
export default {
  components: {
    AddTodoModal
  },
  data () {
    return {
      todoLists: [],
      todoId: null
    }
  },
  mounted () {
    this.getTodos()
  },
  methods: {
    async getTodos () {
      const res = await getTodos()
      const todos = res.data.todos.map(item => {
        return {
          id: item._id,
          content: item.content,
          title: item.title,
          finished: item.finished,
          create_date: moment(item.create_date).format('YYYY/MM/DD HH:mm'),
          update_date: moment(item.update_date).format('YYYY/MM/DD HH:mm')
        }
      })
      todos.reverse()
      this.todoLists = [...todos]
    },
    async handleFinishedTodo (id) {
      const res = await finishedTodo(id)
      if (res.data.data.msg === 'Success') {
        this.getTodos()
      }
    },
    async handleDeleteTodo (id) {
      const res = await deleteTodo(id)
      if (res.data.data.msg === 'Success') {
        this.getTodos()
      }
    },
    async handleUpdateTodo (id) {
      this.todoId = id
      this.openModal()
    },
    openModal () {
      this.$refs.addTodoModal.showModal()
    },
    handleTempTodo (todo) {
      const _todo = { ...todo }
      if (!this.todoId) {
        this.createTodo(_todo)
      } else {
        this.updateTodo(_todo)
      }
      this.$refs.addTodoModal.hideModal()
    },
    async createTodo (todo) {
      try {
        const res = await createTodo(todo)
        if (res.data.data.msg === 'Success') {
          this.getTodos()
        }
      } catch (err) {
        console.log(err)
      }
    },
    async updateTodo (todo) {
      try {
        const res = await updateTodo(todo)
        console.log(res.data.data)
        if (res.data.msg === 'Success') {
          this.getTodos()
        }
      } catch (err) {
        console.log(err)
      }
    }
  }
}
</script>
