import request from '@/utils/request'

// 取得代辦事項清單
export function getTodos () {
  return request({
    method: 'GET',
    url: '/todos'
  })
}

// 新增代辦事項清單
export function createTodo (todoData) {
  console.log(todoData)
  return request({
    method: 'POST',
    url: '/todos',
    data: todoData
  })
}

// 完成代辦事項
export function finishedTodo (id) {
  return request({
    method: 'PUT',
    url: `/todos/finishedTodo/${id}`
  })
}

// 刪除代辦事項
export function deleteTodo (id) {
  return request({
    method: 'DELETE',
    url: `/todos/${id}`
  })
}

// 修改代辦事項清單
export function updateTodo (todoData) {
  console.log(todoData)
  return request({
    method: 'PUT',
    url: `/todos/${todoData.id}`,
    data: todoData
  })
}

// 取得特定的代辦事項
export function getTodo (id) {
  return request({
    method: 'GET',
    url: `/todos/${id}`
  })
}
